import { io } from 'socket.io-client'

const state = {
  token: '',
  socket: null,
  socketConnected: false,
  user: {
    role: {
      name: 'owner',
    },
    notifications: []
  },
}

const mutations = {
  AUTH(state, { user, token }) {
    state.user = user
    state.token = token
  },
  CONNECT(state) {
    if (!state.user) return

    state.socket?.disconnect()
    // state.socket = io('/', {
    // query: `admin=${state.user._id}`,
    // })
    // 
    // state.socket.on('connect', () => {
    // state.socketConnected = true
    // })
    // 
    // setTimeout(() => {
    // if (!state.socketConnected) {
    // state.socket?.disconnect()
    // }
    // }, 2000)
  },
}

export default {
  state,
  mutations,
}
