<template>
  <va-dropdown @anchorClick="open" class="message-dropdown" offset="0, 16px">
    <va-icon-message
      slot="anchor"
      class="message-dropdown__icon"
      :class="{ 'message-dropdown__icon--unread': unread }"
      :color="iconColor"
    />
    <div slot="anchor" class="bage" v-if="unread"></div>
    <div
      v-if="unread"
      class="message-dropdown__content pl-4 pr-4 pt-2 pb-2"
    >
      <div
        v-for="(notification, i) in value"
        :key="i"
        class="message-dropdown__item row pt-1 pb-1 mt-2 mb-2"
        :class="{ 'message-dropdown__item--unread': notification.unread }"
        @click="openNotification(notification)"
      >
        <img
          src="https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fvectorified.com%2Fimages%2Favatar-icon-png-9.jpg&f=1&nofb=1"
          class="message-dropdown__item__avatar mr-2"
        />
        <span class="ellipsis" style="max-width: 85%;">{{
          notification.user
        }}</span>
      </div>
      <div class="row justify--space-between">
        <va-button
          class="ma-0 mb-2 mt-2"
          small
          outline
          @click="clear"
          :disabled="!unread"
          >{{ $t('messages.clear') }}</va-button
        >
      </div>
    </div>
    <div v-else class="message-dropdown__content pl-4 pr-4 pt-2 pb-2">
      No new messages
    </div>
  </va-dropdown>
</template>

<script>
import VaIconMessage from '../../../../../iconset/VaIconMessage'
import { ColorThemeMixin } from '../../../../../services/vuestic-ui'

export default {
  name: 'message-dropdown',
  mixins: [ColorThemeMixin],
  inject: ['contextConfig'],
  components: {
    VaIconMessage,
  },
  props: {
    value: {
      type: Array,
      default: () => [],
    },
    unread: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      computedOptions: [...this.options],
    }
  },
  computed: {
    iconColor() {
      return this.contextConfig.invertedColor ? this.$themes.gray : 'white'
    },
  },
  methods: {
    openNotification(notification) {
      this.$emit('read', notification.id)
      notification.unread = false
      this.$root.$emit('get-user', { id: notification.uid })
      document.dispatchEvent(new Event("click"))
    },
    clear() {
      this.$emit('clear')
    },
  },
}
</script>

