<template>
  <div class="app-navbar-actions">
    <color-dropdown class="app-navbar-actions__item" />
    <message-dropdown
      @clear="clearNotifications"
      @read="readNotification"
      :value="notifications"
      :unread="unread"
      class="app-navbar-actions__item"
    />
    <settings-dropdown
      :is-top-bar.sync="isTopBarProxy"
      class="app-navbar-actions__item"
    />
    <language-dropdown class="app-navbar-actions__item" />
  </div>
</template>

<script>
import LanguageDropdown from './dropdowns/LanguageDropdown'
import MessageDropdown from './dropdowns/MessageDropdown'
import ColorDropdown from './dropdowns/ColorDropdown'
import SettingsDropdown from './dropdowns/SettingsDropdown'
import { ColorThemeMixin } from '../../../../services/vuestic-ui'

import * as Settings from '@/api/Settings'

var msgSound = require('../../../../assets/sounds/message.wav')

export default {
  name: 'app-navbar-actions',
  mixins: [ColorThemeMixin],
  inject: ['contextConfig'],
  components: {
    SettingsDropdown,
    ColorDropdown,
    MessageDropdown,
    LanguageDropdown,
  },
  data: () => ({
    notifications: [],
    lastNUpdate: 0,
  }),
  props: {
    userName: {
      type: String,
      default: '',
    },
    isTopBar: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isTopBarProxy: {
      get() {
        return this.isTopBar
      },
      set(isTopBar) {
        this.$emit('update:isTopBar', isTopBar)
      },
    },
    unread() {
      return this.notifications?.filter(n => n.unread)?.length
    }
  },
  methods: {
    playNewMessageSound() {
      new Audio(msgSound).play()
    },
    updateNotifications(repeat = true) {
      const lastUpdate = +new Date()

      Settings.getNotifications()
        .then(({ data: { notifications } }) => {
          if(repeat) setTimeout(this.updateNotifications, 2000)

          if (this.lastNUpdate > lastUpdate) return

          if (this.notifications?.length < notifications.length) {
            this.playNewMessageSound()
          }

          this.notifications = notifications
          this.unread = this.notifications.filter(item => item.unread).length
        })
        .catch(err => {
          console.log(err)
          if(repeat) setTimeout(this.updateNotifications, 10000)
        })
    },
    readNotification(id) {
      Settings.readNotification(id).then(() => {
        this.lastNUpdate = +new Date()
        this.updateNotifications(false)
      })
    },
    clearNotifications() {
      this.notifications = []
      Settings.clearNotifications().then(() => {
        this.lastNUpdate = +new Date()
        this.updateNotifications(false)
      })
    },
  },
  mounted() {
    this.updateNotifications()
  },
}
</script>

